<template>
  <div>
    <div class="nav">
     <span> <van-icon class="icon" name="passed"  size="13"  /> 100%正品保证</span> 
     <span> <van-icon class="icon" name="passed"  size="13" /> 所有商品精挑细选</span>
     <span> <van-icon class="icon" name="passed"  size="13" /> 售后无忧</span>
  
 
    </div>

    <div class="topnav">
      <span>购物数量:</span>
      <span class="topnav_addnum">{{ addnum }}</span>
    </div>

    <!-- 判断如果购物车没有数据，则会显示提示和推荐 -->
    <van-empty class="custom-image"
  image="https://img01.yzcdn.cn/vant/custom-empty-image.png" description="购物车是空的,去逛一逛、看一看吧" v-show='vaild.length==0' />
    <div v-cloak v-show='vaild.length==0'>
      <div class="commodity">
      
          <span>精品推荐</span>
      </div>
      <div class="main_hot">
        <ul class="main_datail" v-for='item in hotpro' :key='item.id'>
          <router-link :to="`/detail/${item.id}`">
          <li >
              <img :src="item.image" alt="">
              <span>{{item.store_name}}</span>
              <span>￥{{item.price}}</span>
          </li>
          </router-link>
        </ul>
      </div>
    </div>


<!-- 单选选择组件 -->
    <van-swipe-cell v-for="(i, index) in vaild" :key="index" >
      <van-checkbox
        class="check"
        v-model="i.check"
        @change="choice(payload)"
      ></van-checkbox>
      <van-card
        :price="i.productInfo.price"
        :desc="i.productInfo.attrInfo.suk"
        :title="i.productInfo.store_name"
        class="goods-card"
        :thumb="i.productInfo.image"
      >
      </van-card>
      <!-- 购物数量 添加 减少组件 -->
      <van-stepper class="card" v-model="i.cart_num" @plus='addto(i)' @minus='reduce(i)'  />
      <!-- 插槽删除 -->
      <template #right>
        <van-button
          square
          text="删除"
          type="danger"
          class="delete-button"
          @click="Delete(i.id)"
        />
      </template>
    </van-swipe-cell>

    <van-submit-bar :price="price * 100" button-text="提交订单"  @submit="settlement" v-if='vaild.length>0'>
      <!-- 全选和反选 -->
      <van-checkbox v-model="checall" @click='cheall'>全选</van-checkbox>
    </van-submit-bar>
    <div class="null"></div>
  </div>
</template>
<script>
// import { ContactList } from 'vant';
import { mapMutations } from "vuex";
export default {
  name: "Cart",
  data() {
    return {
      vaild: [],
      carenum: 0,
      model: null,
      checall: false,
      hotpro:[],
     
    };
  },
  created() {
    this.cart();
    // 推荐窗口
    this.$axios.get('http://47.115.51.185/api/product/hot?page=1&limit=20').then(d=>{
        console.log(d.data.data)
        this.hotpro=d.data.data;
  })
    
  },
  methods: {
    settlement(){ 
      let storageArr=[];
      for(let a in this.vaild){
        if(this.vaild[a].check == true){
            storageArr.push(this.vaild[a]);
            this.addcart(storageArr);
        }
      }
      if(storageArr.length==0){
            this.$toast("请选择商品");
      }else{
          this.$router.push('/paymentsuccessful');
      }
      
    },
    //购物数量减
     reduce(id){
      console.log(id)
      this.$axios.post('http://47.115.51.185/api/cart/num?id='+id.id+'&number='+(--id.cart_num)).then(d=>{
          console.log(d)
      })
    },
      //购物数量加
    addto(id){
      console.log(id)
      this.$axios.post('http://47.115.51.185/api/cart/num?id='+id.id+'&number='+(++id.cart_num)).then(d=>{
          if(d.data.status == 400){
              this.$toast(d.data.msg);
          }
      })
    },
    cheall(){
        for(let h in this.vaild){
          if(this.checall){
            this.vaild[h].check = true;
          }else{
            this.vaild[h].check = false;
          }
        }
    },
    //单选
    choice() {
      let truea = [];
      // console.log('计算和',this.vaild.filter((item) => item.check).reduce((total, item) => {
      //     return total + item.cart_num;
      //   }, 0) )
      for (let item in this.vaild) {
        if (this.vaild[item].check == true) {
          truea.push(this.vaild[item.index]);
        }
        if (truea.length == this.vaild.length) {
          this.checall = true;
        }else{
          this.checall=false;
        }
      }
      //提交选中的数据存到vuex
      let storageArr=[];
      for(let a in this.vaild){
        if(this.vaild[a].check == true){
            storageArr.push(this.vaild[a]);
            this.addcart(storageArr);
        }
      }
    },
    cart() {
      this.$axios.get("http://47.115.51.185/api/cart/list").then((d) => {
        //   console.log(d.data.data.valid[1].truePrice);
        this.vaild = d.data.data.valid.map((d) => {
          let key = "check";
          let value = false;
          d[key] = value;
          return d;
        });
        console.log("11111111111111111111111");
          console.log(this.vaild);
        return d;
       
      });
    },
    // 删除
    Delete(index) {
      console.log(index);
      this.$axios
        .post("http://47.115.51.185/api/cart/del?ids=" + index)
        .then((d) => {
            // 如果msg==200则删除成功反之失败
          if (d.data.status == 200) {
            this.cart();
            this.$toast("删除成功");
          } else {
            this.$toast("删除失败，请稍后再试");
          }
        });
    },
    ...mapMutations(["addcart"]),
  },
  computed: {
    addnum: function()  {
      return this.vaild.filter((item) => item.check).reduce((total, item) => {
          return total + item.cart_num;
        }, 0)
    },
    //价格计算
    price:function(){
       return this.vaild.filter((item) => item.check).reduce((total, item) => {
          return total + item.productInfo.price*item.cart_num;
          
        }, 0)
    }
  },
};
</script>
<style lang="less" scoped>
[v-cloak]{
    display: none;
}
.nav {
  background-color: #f5f5f5;
  display: flex;
  justify-content: space-around;
  height: 50px;
  color: #666;
  line-height: 50px;
  font-size: 0.8rem;
  span {
    position: relative;
    margin-left: 5px;
    .icon{
    //    margin-top: 2px;
    }
  }
}
.topnav {
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #f5f5f5;
  span {
    padding: 10px;
    line-height: 50px;
  }
  .topnav_addnum {
    color: red;
  }
}

.goods-card {
  margin: 0;
  background-color: white;
}

.delete-button {
  height: 100%;
}
.van-card {
  position: relative;
  margin-left: 20px;
}
.card {
  position: absolute;
  bottom: 0px;
  right: 0px;
}
.check {
  position: absolute;
  left: 5px;
  top: 44px;
  z-index: 99;
}
.van-submit-bar {
  margin-bottom: 50px;
}
.null{
  padding-bottom: 100px;
  background-color: white;
}
.commodity{
  text-align: center;
}
.main_hot{
  display: flex;
  flex-wrap: wrap;
.main_datail{
  
  li{
    display: flex;
    flex-direction: column;
    width: 45%;
    margin-left: 3vw;
    padding: 1vh 0vw;
    img{
      width: 45vw;
      height: 40vw;
      border-radius: 6px;
    }
    span:nth-child(2){
       display: block;
          text-overflow: ellipsis;
          overflow: hidden;
          width: 40vw;
          white-space: nowrap;
          color: #666;
          font-size:15px ;
    }
     span:nth-child(3){
          color: red;
          font-size:14px ;
    }
  }
}
}
</style>